export default "import path from 'path'\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  component: dirs.pop(),\n  type: dirs.pop(),\n}\n\ndemo.htmlSource = `\n<div class=\"list list--default list-space--small align-items-center demo--toggle-progress\">\n  <button type=\"button\" class=\"btn btn--default\">\n    <span>\n      Toggle 0\n    </span>\n    <span class=\"loader loader--y loader--js\">\n        <span class=\"filler\">\n          <span></span><span></span>\n        </span>\n      </span>\n  </button>\n  <button type=\"button\" class=\"btn btn--default\">\n    <span>\n      Toggle 1\n    </span>\n    <span class=\"loader loader--y loader--js\">\n        <span class=\"filler\">\n          <span></span><span></span>\n        </span>\n      </span>\n  </button>\n  <button type=\"button\" class=\"btn btn--default\">\n    <span>\n      Toggle 2\n    </span>\n    <span class=\"loader loader--y loader--js\">\n        <span class=\"filler\">\n          <span></span><span></span>\n        </span>\n      </span>\n  </button>\n  <button type=\"button\" class=\"btn btn--default\">\n    <span>\n      Toggle 3\n    </span>\n    <span class=\"loader loader--y loader--js\">\n        <span class=\"filler\">\n          <span></span><span></span>\n        </span>\n      </span>\n  </button>\n  <div class=\"note note--default note--background toggle-block\">\n    Target 0\n    <div class=\"loader loader--x loader-size--bottom loader--js\">\n      <div class=\"filler\">\n        <span></span><span></span>\n      </div>\n    </div>\n  </div>\n  <div class=\"note note--default note--background toggle-block\">\n    Target 1\n    <div class=\"loader loader--x loader-size--bottom loader--js\">\n      <div class=\"filler\">\n        <span></span><span></span>\n      </div>\n    </div>\n  </div>\n  <div class=\"note note--default note--background toggle-block\">\n    Target 2\n    <div class=\"loader loader--x loader-size--bottom loader--js\">\n      <div class=\"filler\">\n        <span></span><span></span>\n      </div>\n    </div>\n  </div>\n  <div class=\"note note--default note--background toggle-block\">\n    Target 3\n    <span class=\"loader loader--x loader-size--bottom loader--js\">\n      <span class=\"filler\">\n        <span></span><span></span>\n      </span>\n    </span>\n  </div>\n</div>\n`\n"